.multimint form{
    background-color: #2f6d9f;
    padding: 20px;
    border-radius: 10px;
    min-width: 800px;
}
/* Style inputs, select elements and textareas */
.multimint input[type=text], select, textarea{
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    resize: vertical;
  }
  
  /* Style the label to display next to the inputs */
  .multimint label {
    padding: 12px 12px 12px 0;
    display: inline-block;
  }
  
  /* Style the submit button */
  .multimint  input[type=submit] {
    background-color: #04AA6D;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    float: right;
  }
  .multimint  .multimint {
    margin-top: 100px;
  }
  /* Style the container */
  .multimint  .container {
    border-radius: 5px;
    background-color: none;
    padding: 20px 0;
    margin-top: 10%;
  }
  
  /* Floating column for labels: 25% width */
  .multimint .col-25 {
    float: left;
    width: 25%;
    margin-top: 6px;
  }
  
  /* Floating column for inputs: 75% width */
  .multimint .col-75 {
    float: left;
    width: 75%;
    margin-top: 6px;
  }
  
  /* Clear floats after the columns */
  .multimint .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */
  @media screen and (max-width: 600px) {
    .multimint  .col-25, .col-75, input[type=submit] {
      width: 100%;
      margin-top: 0;
    }
  }
  .multimint.uplaod-img {
    border: 4px solid rgb(219, 204, 204);
    min-height: 100%;
    min-width: 100%;
    background-color: cyan;
}
