#NftDetail_outer1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-top: 65%;
  margin-left: 7.5%;
}


.NftDetail_info1 {
  display: flex;
  flex-direction: column;
  margin-top: 10%;
  margin-bottom: 5%;
  background-color: rgb(182, 230, 181);
  padding-left: 0%;
  padding-right: 18%;
  justify-content: space-between;
}

.NftDetail_left1 {
  margin-right: 10%;
  margin-left: 10%;
  display: flex;
  flex-direction: column;
  width: 40%;
}


.NftDetail_right1 {
  margin-top: -1%;
  margin-right: 8%;
  width: 60%;
  margin-top: 8%;
}

.NftDetail_nft1 {
  font-size: 20px;
  padding-left: 10%;

}

.NftDetail_name1 {
  font-size: 50px;
  margin-top: 2%;
  font-weight: bold;
  padding-left: 10%;
}

.NftDetail_button1 {
  background-color: #9ef4ff;
  border-radius: 100px;
  box-shadow: rgba(44, 187, 99, .2) 0 -25px 18px -14px inset, rgba(44, 187, 99, .15) 0 1px 2px, rgba(44, 187, 99, .15) 0 2px 4px, rgba(44, 187, 99, .15) 0 4px 8px, rgba(44, 187, 99, .15) 0 8px 16px, rgba(44, 187, 99, .15) 0 16px 32px;
  color: rgb(5, 139, 192);
  cursor: pointer;
  display: inline-block;
  font-family: CerebriSans-Regular, -apple-system, system-ui, Roboto, sans-serif;
  padding: 7px 20px;
  text-align: center;
  text-decoration: none;
  transition: all 250ms;
  border: 0;
  font-size: 20px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin: 3%;
}

.NftDetail_button1:hover {
  box-shadow: rgba(44, 118, 187, 0.35) 0 -25px 18px -14px inset, rgba(44, 187, 99, .25) 0 1px 2px, rgba(44, 187, 99, .25) 0 2px 4px, rgba(44, 187, 99, .25) 0 4px 8px, rgba(44, 187, 99, .25) 0 8px 16px, rgba(44, 187, 99, .25) 0 16px 32px;
  transform: scale(1.05) rotate(-1deg);
}

.NftDetail_buttons-section1 {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}


.NftDetail_space1 {
  padding: 3%;
  display: flex;
  justify-content: space-between;
  padding-left: 10%;
  font-size: 120%;
  flex-wrap: wrap;
}

.NftDetail_f {
  display: flex;
  flex-direction: row;
  margin-top: 1%;
  margin-left: 5%;
}


.NftDetail_background-img {
  height: 408px;
  width: 683px;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 4% auto;
  border: 1px solid #ffffff;
  color: hwb(124deg 30% 17%);
  border-radius: 4px;
  box-shadow: 0px 0px 5px #777c99;
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 2%;
}

.NftDetail_name {
  font-size: 250%;
  padding: 2.5%;
  padding-left: 5%;
  color: aqua;
}

.NftDetail_hash {
  font-size: 150%;
  padding: 1%;
  padding-left: 5%;
  color: aqua;
}




.NftDetail_box {

  width: 100%;
  height: auto;
  background: #111845a6;
  box-sizing: border-box;
  overflow: hidden;
  box-shadow: 0 20px 50px rgb(23, 32, 90);
  border: 2px solid #2a3cad;
  color: white;
  padding: 14px;
  margin-top: 0;
}




.NftDetail_box .NftDetail_content {
  border: 1px solid #f0a591;
  padding: 20px;
  text-align: left;
  box-shadow: 0 5px 10px rgba(9, 0, 0, 0.5);

}

.NftDetail_box {
  padding: 3px;
}

.NftDetail_box span {
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;

}


