.NftDetail_con {
  margin-top: 5%;
  word-wrap: break-word;
}




.NftDetail_container2 {
  max-width: 1140px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  height: auto;
  margin-top: 3%;
}

.NftDetail_container2 a {
  text-decoration: none;
  color: hwb(124deg 30% 17%);
}

.NftDetail_grid-item {
  width: 100%;
  padding: 0 15px;
  margin-bottom: 30px;
}



@media (min-width: 992px) {
  .NftDetail_grid-item {
    width: 33.33%;
  }
}

.NftDetail_item-style {
  background: #20202d;
  border-radius: 10px;
  transition: 0.3s;
}

.NftDetail_item-style:hover {
  box-shadow: rgb(255 255 255 / 25%) 0px 0px 5px 5px;
}

.NftDetail_item-style .thumb {
  position: relative;
  overflow: hidden;
  display: block;
  border-radius: 10px 10px 0 0;
  margin-top: 37%;
}

.NftDetail_item-style .thumb img {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: 0.5s;
}

.NftDetail_item-style:hover .thumb img {
  transform: scale(1.21);
}

i.ri-heart-fill {
  padding: 0 5px;
  color: #ff512f;
  padding-right: 5px;
  font-size: 18px;
}

.NftDetail_reaction-btn {
  border: none;
  font-size: 14px;
  font-weight: 500;
  position: absolute;
  top: 15px;
  right: 15px;
  color: #fff;
  background: #343444;
  border-radius: 6px;
  padding: 5px 6px;
  min-width: 45px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.NftDetail_content {
  padding: 15px 20px;
}

.NftDetail_title {
  font-size: 18px;
  padding: 0;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.NftDetail_title a {
  color: aqua;
}

.NftDetail_profile a {
  position: relative;
  z-index: 1;
  display: block;
}

.NftDetail_d-flex-center {
  display: flex;
  align-items: center;
}

.NftDetail_d-flex-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.NftDetail_profile img.image-avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid #fff;
}

.NftDetail_profile a.author-text {
  padding-left: 7px;
}

.NftDetail_profile {
  margin: 10px 0;
  width: 400px;
}

.NftDetail_btn span {
  display: inline-flex;
  z-index: 1;
}

.NftDetail_btn i {
  padding-right: 8px;
  transition: 0.1s;
}

.NftDetail_btn-cart {
  position: relative;
  overflow: hidden;
  font-size: 18px;
  padding: 6px 25px;
  display: inline-flex;
  align-items: center;
  color: aqua;
  border-radius: 50px;
  border: 2px solid rgba(255, 255, 255, 0.7);
  height: 50px;
  min-height: 50px;
  transition: 0.7s;
}

.NftDetail_btn-cart:hover {
  color: #fff;
  transform: translateY(-2px);
}

.NftDetail_btn-outline::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(97.3deg, rgb(32 38 117) 0%, rgb(56, 151, 156) 100%);
  transition: transform 0.7s;
  transform-origin: right;
  transform: scaleX(0);
  z-index: 0;
  border-radius: inherit;
}

.NftDetail_btn-outline:hover::before {
  transform: scaleX(1);
  transform-origin: left;
  transition: transform 0.7s;
}

.NftDetail_head h1 {
  color: aqua;
}


